import React, { useState, useEffect, useRef } from 'react';
import {
  TextField,
  Autocomplete,
  Box,
  Typography,
  Chip,
  Paper,
  IconButton,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  Alert,
  Tooltip,
} from '@mui/material';
import { 
  Search as SearchIcon,
  Delete as DeleteIcon,
  History as HistoryIcon,
  TipsAndUpdates as TipsIcon,
} from '@mui/icons-material';
import { stockAPI } from '../utils/api';

const StockSearch = ({ value, onChange, onSelect }) => {
  const [options, setOptions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [inputValue, setInputValue] = useState('');
  const [showHistory, setShowHistory] = useState(false);
  const [searchHistory, setSearchHistory] = useState([]);
  const [showTips, setShowTips] = useState(false);
  const searchTimeoutRef = useRef(null);

  // 
  const searchTips = [
    { type: '', example: '000001600036', desc: '6' },
    { type: '', example: '', desc: '' },
    { type: '', example: 'pabzsyhzmj', desc: '' },
    { type: '', example: '', desc: '' },
  ];

  // 
  const popularStocks = [
    { code: '000001', name: '', sector: '', tips: 'pab' },
    { code: '000002', name: 'A', sector: '', tips: 'wk' },
    { code: '600000', name: '', sector: '', tips: 'pdfyh' },
    { code: '600036', name: '', sector: '', tips: 'zsyh' },
    { code: '000858', name: '', sector: '', tips: 'wly' },
    { code: '601717', name: '', sector: '', tips: 'zmj' },
  ];

  // localStorage
  useEffect(() => {
    const savedHistory = localStorage.getItem('stockSearchHistory');
    if (savedHistory) {
      try {
        setSearchHistory(JSON.parse(savedHistory));
      } catch (error) {
        console.error(':', error);
        setSearchHistory([]);
      }
    }
  }, []);

  // localStorage
  const saveSearchHistory = (history) => {
    try {
      localStorage.setItem('stockSearchHistory', JSON.stringify(history));
    } catch (error) {
      console.error(':', error);
    }
  };

  // 
  const addToHistory = (stock) => {
    const newHistory = [
      stock,
      ...searchHistory.filter(item => item.code !== stock.code)
    ].slice(0, 10); // 10
    
    setSearchHistory(newHistory);
    saveSearchHistory(newHistory);
  };

  // 
  const removeFromHistory = (codeToRemove) => {
    const newHistory = searchHistory.filter(item => item.code !== codeToRemove);
    setSearchHistory(newHistory);
    saveSearchHistory(newHistory);
  };

  // 
  const clearHistory = () => {
    setSearchHistory([]);
    localStorage.removeItem('stockSearchHistory');
  };

  // 
  const searchStocks = async (query) => {
    if (!query.trim()) {
      setOptions([]);
      return;
    }

    setLoading(true);
    try {
      const response = await stockAPI.searchStocks(query, 8);
      if (response.success) {
        setOptions(response.stocks);
        console.log(':', response.stocks); // 
      } else {
        setOptions([]);
      }
    } catch (error) {
      console.error(':', error);
      setOptions([]);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleInputChange = (event, newInputValue) => {
    setInputValue(newInputValue);
    setShowHistory(false);
    setShowTips(false);
    
    // 
    if (searchTimeoutRef.current) {
      clearTimeout(searchTimeoutRef.current);
    }
    
    // 200ms
    searchTimeoutRef.current = setTimeout(() => {
      searchStocks(newInputValue);
    }, 200);
  };

  // 
  const handleChange = (event, newValue) => {
    // 
    let valueToSet = '';
    let selectedStock = null;
    
    if (typeof newValue === 'string') {
      valueToSet = newValue;
    } else if (newValue && typeof newValue === 'object' && newValue.code) {
      valueToSet = newValue.code;
      selectedStock = newValue;
    }
    
    onChange(valueToSet);
    if (valueToSet && onSelect) {
      onSelect(valueToSet);
    }
    
    // 
    if (selectedStock) {
      addToHistory(selectedStock);
    }
    
    setShowHistory(false);
    setShowTips(false);
  };

  // 
  const handleFocus = () => {
    if (!inputValue.trim()) {
      if (searchHistory.length > 0) {
        setShowHistory(true);
      } else {
        setShowTips(true);
      }
    }
  };

  // 
  const handleBlur = () => {
    // 
    setTimeout(() => {
      setShowHistory(false);
      setShowTips(false);
    }, 200);
  };

  // 
  const selectFromHistory = (stock) => {
    setInputValue(stock.code);
    onChange(stock.code);
    if (onSelect) onSelect(stock.code);
    setShowHistory(false);
  };

  // 
  const selectFromPopular = (stock) => {
    setInputValue(stock.code);
    onChange(stock.code);
    if (onSelect) onSelect(stock.code);
    addToHistory(stock);
    setShowTips(false);
  };

  // 
  useEffect(() => {
    return () => {
      if (searchTimeoutRef.current) {
        clearTimeout(searchTimeoutRef.current);
      }
    };
  }, []);

  return (
    <Box sx={{ position: 'relative' }}>
      <Autocomplete
        value={value}
        onChange={handleChange}
        inputValue={inputValue}
        onInputChange={handleInputChange}
        options={options}
        loading={loading}
        freeSolo
        onFocus={handleFocus}
        onBlur={handleBlur}
        getOptionLabel={(option) => {
          if (typeof option === 'string') return option;
          if (option && typeof option === 'object' && option.code) {
            return option.name ? `${option.code} ${option.name}` : option.code;
          }
          return '';
        }}
        renderInput={(params) => (
          <TextField
            {...params}
            label=""
            placeholder="000001pab"
            variant="outlined"
            fullWidth
            InputProps={{
              ...params.InputProps,
              startAdornment: (
                <Box sx={{ display: 'flex', alignItems: 'center', mr: 1 }}>
                  <SearchIcon color="action" />
                  <Tooltip title="">
                    <IconButton
                      size="small"
                      onClick={() => setShowTips(!showTips)}
                      sx={{ ml: 0.5 }}
                    >
                      <TipsIcon fontSize="small" color="primary" />
                    </IconButton>
                  </Tooltip>
                </Box>
              ),
            }}
            sx={{
              '& .MuiOutlinedInput-root': {
                '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                  borderColor: 'primary.main',
                  borderWidth: 2,
                }
              }
            }}
          />
        )}
        renderOption={(props, option) => {
          const { key, ...otherProps } = props;
          return (
            <Box component="li" key={key} {...otherProps}>
              <Box sx={{ display: 'flex', flexDirection: 'column', width: '100%' }}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                  <Typography variant="body1" sx={{ fontWeight: 'bold', color: 'primary.main' }}>
                    {option.code}
                  </Typography>
                  <Chip 
                    label={option.sector || ''} 
                    size="small" 
                    variant="outlined"
                    color={option.sector === '' ? 'primary' : 
                           option.sector === '' ? 'secondary' :
                           option.sector === '' ? 'warning' : 'default'}
                    sx={{ fontSize: '0.75rem' }}
                  />
                </Box>
                <Typography variant="body2" color="text.secondary">
                  {option.name}
                </Typography>
              </Box>
            </Box>
          );
        }}
        noOptionsText={
          <Box sx={{ p: 2, textAlign: 'center' }}>
            <Typography variant="body2" color="text.secondary">
              
            </Typography>
            <Typography variant="caption" color="text.secondary" sx={{ mt: 1, display: 'block' }}>
              
            </Typography>
          </Box>
        }
        loadingText={
          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', p: 2 }}>
            <Typography variant="body2">...</Typography>
          </Box>
        }
        filterOptions={(x) => x} // 
      />
      
      {/*  */}
      {showHistory && searchHistory.length > 0 && (
        <Paper 
          sx={{ 
            position: 'absolute', 
            top: '100%', 
            left: 0, 
            right: 0, 
            zIndex: 1000, 
            mt: 1, 
            maxHeight: 300, 
            overflow: 'auto',
            bgcolor: 'background.paper',
            border: 1,
            borderColor: 'divider',
            boxShadow: 3,
          }}
        >
          <Box sx={{ p: 1, borderBottom: 1, borderColor: 'divider', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="subtitle2" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              <HistoryIcon fontSize="small" />
              
            </Typography>
            <Chip 
              label="" 
              size="small" 
              variant="outlined" 
              clickable
              onClick={clearHistory}
              sx={{ fontSize: '0.75rem' }}
            />
          </Box>
          {searchHistory.map((stock) => (
            <ListItem 
              key={stock.code} 
              button 
              onClick={() => selectFromHistory(stock)}
              sx={{ 
                py: 1,
                '&:hover': {
                  bgcolor: 'action.hover',
                }
              }}
            >
              <ListItemText
                primary={
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <Typography variant="body2" sx={{ fontWeight: 'bold', color: 'primary.main' }}>
                      {stock.code}
                    </Typography>
                    <Chip 
                      label={stock.sector || ''} 
                      size="small" 
                      variant="outlined"
                      sx={{ fontSize: '0.7rem', height: 20 }}
                    />
                  </Box>
                }
                secondary={stock.name}
              />
              <ListItemSecondaryAction>
                <IconButton
                  edge="end"
                  size="small"
                  onClick={(e) => {
                    e.stopPropagation();
                    removeFromHistory(stock.code);
                  }}
                  sx={{ 
                    color: 'error.main',
                    '&:hover': {
                      bgcolor: 'error.light',
                      color: 'white',
                    }
                  }}
                >
                  <DeleteIcon fontSize="small" />
                </IconButton>
              </ListItemSecondaryAction>
            </ListItem>
          ))}
        </Paper>
      )}
      
      {/*  */}
      {showTips && (
        <Paper 
          sx={{ 
            position: 'absolute', 
            top: '100%', 
            left: 0, 
            right: 0, 
            zIndex: 1000, 
            mt: 1, 
            maxHeight: 400, 
            overflow: 'auto',
            bgcolor: 'background.paper',
            border: 1,
            borderColor: 'divider',
            boxShadow: 3,
          }}
        >
          {/*  */}
          <Box sx={{ p: 2, borderBottom: 1, borderColor: 'divider' }}>
            <Typography variant="subtitle2" sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
              <TipsIcon fontSize="small" color="primary" />
              
            </Typography>
            {searchTips.map((tip, index) => (
              <Box key={index} sx={{ mb: 1 }}>
                <Typography variant="caption" sx={{ fontWeight: 'bold', color: 'primary.main' }}>
                  {tip.type}:
                </Typography>
                <Typography variant="caption" sx={{ ml: 1, color: 'text.secondary' }}>
                  {tip.example} - {tip.desc}
                </Typography>
              </Box>
            ))}
          </Box>
          
          {/*  */}
          <Box sx={{ p: 2 }}>
            <Typography variant="subtitle2" color="primary" gutterBottom>
               
            </Typography>
            {popularStocks.map((stock) => (
              <Box
                key={stock.code}
                onClick={() => selectFromPopular(stock)}
                sx={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  alignItems: 'center',
                  p: 1,
                  cursor: 'pointer',
                  borderRadius: 1,
                  '&:hover': {
                    bgcolor: 'action.hover',
                  }
                }}
              >
                <Box>
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <Typography variant="body2" sx={{ fontWeight: 'bold', color: 'primary.main' }}>
                      {stock.code}
                    </Typography>
                    <Chip 
                      label={stock.sector} 
                      size="small" 
                      variant="outlined"
                      sx={{ fontSize: '0.7rem', height: 18 }}
                    />
                  </Box>
                  <Typography variant="caption" color="text.secondary">
                    {stock.name}
                  </Typography>
                </Box>
                <Typography variant="caption" color="text.secondary" sx={{ fontStyle: 'italic' }}>
                  {stock.tips}
                </Typography>
              </Box>
            ))}
          </Box>
        </Paper>
      )}
      
      {/*  */}
      <Box sx={{ mt: 2 }}>
        <Alert 
          severity="info" 
          sx={{ 
            bgcolor: 'primary.50',
            '& .MuiAlert-message': { fontSize: '0.875rem' }
          }}
        >
          <Typography variant="body2" sx={{ mb: 1 }}>
            <strong> </strong>
          </Typography>
          <Typography variant="caption" color="text.secondary">
             pabzsyhzmj<br/>
             <br/>
             <br/>
             
          </Typography>
        </Alert>
      </Box>
    </Box>
  );
};

export default StockSearch; 