import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  CardActions,
  Button,
  Chip,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  FormControlLabel,
  Checkbox,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Divider,
  List,
  ListItem,
  ListItemText,
  Paper
} from '@mui/material';
import {
  TrendingUp,
  Security,
  AccountBalance,
  Psychology,
  Agriculture,
  Build,
  Info,
  PlayArrow,
  Speed,
  ExpandMore,
  FilterList,
  BusinessCenter,
  Computer,
  LocalHospital,
  Restaurant,
  Whatshot,
  DirectionsCar,
  Engineering
} from '@mui/icons-material';
import RealTimeProgress from '../components/RealTimeProgress';

// 
const MARKETS = [
  { id: 'all', name: '', description: 'A', color: '#1976d2' },
  { id: 'main_board_sh', name: 'A', description: '600xxx', color: '#2196f3' },
  { id: 'main_board_sz', name: 'A', description: '000xxx', color: '#03a9f4' },
  { id: 'gem', name: '', description: '300xxx', color: '#4caf50' },
  { id: 'star_market', name: '', description: '688xxx', color: '#ff9800' },
  { id: 'beijing', name: '', description: '8xxxxx', color: '#9c27b0' }
];

// 
const INDUSTRIES = [
  { id: 'all', name: '', icon: <BusinessCenter />, color: '#607d8b' },
  { id: 'banking', name: '', icon: <AccountBalance />, color: '#1976d2' },
  { id: 'insurance', name: '', icon: <Security />, color: '#1565c0' },
  { id: 'securities', name: '', icon: <TrendingUp />, color: '#0d47a1' },
  { id: 'technology', name: '', icon: <Computer />, color: '#2e7d32' },
  { id: 'healthcare', name: '', icon: <LocalHospital />, color: '#c62828' },
  { id: 'consumer', name: '', icon: <Restaurant />, color: '#f57c00' },
  { id: 'energy', name: '', icon: <Whatshot />, color: '#5d4037' },
  { id: 'automotive', name: '', icon: <DirectionsCar />, color: '#455a64' },
  { id: 'manufacturing', name: '', icon: <Engineering />, color: '#37474f' },
  { id: 'real_estate', name: '', icon: <Build />, color: '#6a1b9a' },
  { id: 'agriculture', name: '', icon: <Agriculture />, color: '#689f38' }
];

// 
const PRESET_STRATEGIES = [
  {
    id: 'blue_chip_stable',
    name: '',
    description: '',
    icon: <AccountBalance />,
    color: '#1976d2',
    risk_level: '',
    expected_return: '8-15%',
    parameters: {
      pe_min: 5,
      pe_max: 25,
      pb_min: 0.5,
      pb_max: 3,
      roe_min: 8,
      market_cap_min: 1000,
      debt_ratio_max: 60
    },
    selection_criteria: [
      'PE: 5-25',
      'PB: 0.5-3',
      'ROE  8%',
      '  1000',
      '  60%',
      ''
    ]
  },
  {
    id: 'high_dividend',
    name: '',
    description: '',
    icon: <TrendingUp />,
    color: '#388e3c',
    risk_level: '',
    expected_return: '6-12%',
    parameters: {
      dividend_yield_min: 4,
      pe_min: 3,
      pe_max: 20,
      pb_min: 0.3,
      pb_max: 2.5,
      roe_min: 5,
      payout_ratio_min: 30,
      payout_ratio_max: 70
    },
    selection_criteria: [
      '  4%',
      'PE: 3-20',
      'PB: 0.3-2.5',
      'ROE  5%',
      ': 30-70%',
      ''
    ]
  },
  {
    id: 'quality_growth',
    name: '',
    description: '',
    icon: <Psychology />,
    color: '#f57c00',
    risk_level: '',
    expected_return: '15-25%',
    parameters: {
      pe_min: 10,
      pe_max: 35,
      pb_min: 1,
      pb_max: 5,
      roe_min: 15,
      revenue_growth_min: 15,
      profit_growth_min: 15,
      market_cap_min: 100
    },
    selection_criteria: [
      'PE: 10-35',
      'PB: 1-5',
      'ROE  15%',
      '  15%',
      '  15%',
      '  100'
    ]
  },
  {
    id: 'value_investment',
    name: '',
    description: '',
    icon: <AccountBalance />,
    color: '#7b1fa2',
    risk_level: '',
    expected_return: '10-18%',
    parameters: {
      pe_min: 3,
      pe_max: 15,
      pb_min: 0.3,
      pb_max: 2,
      roe_min: 8,
      market_cap_min: 50,
      debt_ratio_max: 50
    },
    selection_criteria: [
      'PE: 3-15',
      'PB: 0.3-2',
      'ROE  8%',
      '  50',
      '  50%',
      ''
    ]
  },
  {
    id: 'small_cap_growth',
    name: '',
    description: '',
    icon: <TrendingUp />,
    color: '#d32f2f',
    risk_level: '',
    expected_return: '20-40%',
    parameters: {
      pe_min: 15,
      pe_max: 60,
      pb_min: 2,
      pb_max: 10,
      roe_min: 15,
      market_cap_min: 20,
      market_cap_max: 200,
      revenue_growth_min: 20
    },
    selection_criteria: [
      'PE: 15-60',
      'PB: 2-10',
      'ROE  15%',
      ': 20-200',
      '  20%',
      ''
    ]
  },
  {
    id: 'cyclical_rotation',
    name: '',
    description: '',
    icon: <Build />,
    color: '#455a64',
    risk_level: '',
    expected_return: '12-25%',
    parameters: {
      pe_min: 8,
      pe_max: 30,
      pb_min: 0.8,
      pb_max: 4,
      roe_min: 10,
      market_cap_min: 100,
      cyclical_score_min: 7
    },
    selection_criteria: [
      'PE: 8-30',
      'PB: 0.8-4',
      'ROE  10%',
      '  100',
      '  7',
      ''
    ]
  }
];

const PresetStrategies = () => {
  // 
  const [selectedStrategy, setSelectedStrategy] = useState(null);
  const [detailDialogOpen, setDetailDialogOpen] = useState(false);
  const [filterDialogOpen, setFilterDialogOpen] = useState(false);
  const [scanResults, setScanResults] = useState(null);
  const [progress, setProgress] = useState(0);
  const [currentStep, setCurrentStep] = useState('');
  const [executingStrategy, setExecutingStrategy] = useState(null);
  const [realTimeData, setRealTimeData] = useState({});
  const [progressTimer, setProgressTimer] = useState(null);

  // 
  const [selectedMarkets, setSelectedMarkets] = useState(['all']);
  const [selectedIndustries, setSelectedIndustries] = useState(['all']);
  const [customParameters, setCustomParameters] = useState({});
  const [isFullMarket, setIsFullMarket] = useState(false);

  // 
  useEffect(() => {
    return () => {
      if (progressTimer) {
        clearInterval(progressTimer);
      }
    };
  }, [progressTimer]);

  // 
  const getRiskColor = (riskLevel) => {
    const colorMap = {
      '': '#4caf50',
      '': '#ff9800',
      '': '#f57c00',
      '': '#f44336'
    };
    return colorMap[riskLevel] || '#757575';
  };

  // 
  const handleMarketChange = (marketId) => {
    if (marketId === 'all') {
      setSelectedMarkets(['all']);
    } else {
      const newMarkets = selectedMarkets.includes('all') 
        ? [marketId]
        : selectedMarkets.includes(marketId)
          ? selectedMarkets.filter(id => id !== marketId)
          : [...selectedMarkets.filter(id => id !== 'all'), marketId];
      
      setSelectedMarkets(newMarkets.length === 0 ? ['all'] : newMarkets);
    }
  };

  // 
  const handleIndustryChange = (industryId) => {
    if (industryId === 'all') {
      setSelectedIndustries(['all']);
    } else {
      const newIndustries = selectedIndustries.includes('all') 
        ? [industryId]
        : selectedIndustries.includes(industryId)
          ? selectedIndustries.filter(id => id !== industryId)
          : [...selectedIndustries.filter(id => id !== 'all'), industryId];
      
      setSelectedIndustries(newIndustries.length === 0 ? ['all'] : newIndustries);
    }
  };

  // 
  const startRealTimeProgress = (isFullMarketScan) => {
    console.log(' ...');
    
    const timer = setInterval(() => {
      // 
      setRealTimeData(prev => ({
        ...prev,
        currentStock: `${Math.floor(Math.random() * 1000) + 1}`,
        qualifiedCount: Math.floor(Math.random() * 50) + 1,
        dataSource: Math.random() > 0.5 ? 'TuShare' : 'AkShare',
        analysisSpeed: `${(Math.random() * 10 + 5).toFixed(1)}/`
      }));
    }, 2000);
    
    setProgressTimer(timer);
  };

  const stopRealTimeProgress = () => {
    if (progressTimer) {
      clearInterval(progressTimer);
      setProgressTimer(null);
    }
  };

  // 
  const executeStrategy = async (strategy, scanType = 'quick') => {
    const isFullMarketScan = scanType === 'full';
    
    console.log(` : ${strategy.name} (${scanType} )`);
    console.log(' :', {
      markets: selectedMarkets,
      industries: selectedIndustries,
      parameters: { ...strategy.parameters, ...customParameters }
    });
    
    setProgress(0);
    setScanResults(null);
    setExecutingStrategy(strategy);
    setCurrentStep(`${scanType}...`);

    // 
    startRealTimeProgress(isFullMarketScan);

    // 
    setProgress(5);

    // 
    let progressInterval;
    let hasStarted = false;
    let startTime = Date.now();
    let executionId = null;

    try {
      let progressCheckCount = 0;
      const maxProgressChecks = isFullMarketScan ? 300 : 100; // 
      
      // 
      const monitorProgress = () => {
        progressInterval = setInterval(async () => {
          progressCheckCount++;
          
          // 
          if (progressCheckCount > maxProgressChecks) {
            console.warn(' ');
            clearInterval(progressInterval);
            return;
          }
          
          try {
            // TODO: API
            // 
            
            // 
            if (!hasStarted) {
              setProgress(prev => Math.min(prev + 1, 15)); // 15%
              setCurrentStep(' ...');
              return;
            }
            
            // 
            const currentTime = Date.now();
            const elapsed = (currentTime - startTime) / 1000; // 
            
            // 
            let estimatedProgress;
            let currentMessage;
            
            if (isFullMarketScan) {
              // 3-5
              if (elapsed < 30) {
                estimatedProgress = 20 + (elapsed / 30) * 25; // 20% -> 45%
                currentMessage = ` A... (${Math.floor(elapsed)}s)`;
              } else if (elapsed < 120) {
                estimatedProgress = 45 + ((elapsed - 30) / 90) * 35; // 45% -> 80%
                const currentStock = Math.floor((elapsed - 30) * 50 / 90);
                currentMessage = ` ${currentStock}/5000... (${Math.floor(elapsed)}s)`;
              } else if (elapsed < 180) {
                estimatedProgress = 80 + ((elapsed - 120) / 60) * 15; // 80% -> 95%
                currentMessage = ` ... (${Math.floor(elapsed)}s)`;
              } else {
                estimatedProgress = Math.min(95 + ((elapsed - 180) / 60) * 4, 99); // 95% -> 99%
                currentMessage = ` ... (${Math.floor(elapsed)}s)`;
              }
            } else {
              // 1-2
              if (elapsed < 15) {
                estimatedProgress = 20 + (elapsed / 15) * 25; // 20% -> 45%
                currentMessage = ` ... (${Math.floor(elapsed)}s)`;
              } else if (elapsed < 60) {
                estimatedProgress = 45 + ((elapsed - 15) / 45) * 40; // 45% -> 85%
                const currentStock = Math.floor((elapsed - 15) * 4);
                currentMessage = ` ${currentStock}/200... (${Math.floor(elapsed)}s)`;
              } else if (elapsed < 90) {
                estimatedProgress = 85 + ((elapsed - 60) / 30) * 10; // 85% -> 95%
                currentMessage = ` ... (${Math.floor(elapsed)}s)`;
              } else {
                estimatedProgress = Math.min(95 + ((elapsed - 90) / 30) * 4, 99); // 95% -> 99%
                currentMessage = ` ... (${Math.floor(elapsed)}s)`;
              }
            }
            
            setProgress(Math.floor(estimatedProgress));
            setCurrentStep(currentMessage);
            
            // 20%
            if (Math.floor(estimatedProgress) % 20 === 0) {
              console.log(` : ${Math.floor(estimatedProgress)}% - ${currentMessage}`);
            }
            
          } catch (error) {
            console.error(' :', error);
          }
        }, isFullMarketScan ? 2000 : 1000); // 21
      };
      
      console.log(` ${scanType}API...`);
      
      // API
      const apiEndpoint = isFullMarketScan ? '/api/strategies/full-market-scan' : '/api/strategies/preset-scan';
      
      const requestBody = isFullMarketScan ? {
        strategy_id: strategy.id,
        start_date: '20220101',
        end_date: new Date().toISOString().slice(0, 10).replace(/-/g, ''),
        min_score: 60.0,
        batch_size: 100,
        markets: selectedMarkets,
        industries: selectedIndustries
      } : {
        strategy_id: strategy.id,
        strategy_name: strategy.name,
        parameters: { ...strategy.parameters, ...customParameters },
        markets: selectedMarkets,
        industries: selectedIndustries
      };
      
      // 
      monitorProgress();
      
      // 
      await new Promise(resolve => setTimeout(resolve, 500));
      
      // 
      hasStarted = true;
      setProgress(18);
      setCurrentStep(' API...');
      
      // API
      const response = await fetch(apiEndpoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(requestBody)
      });

      console.log(` ${scanType}API:`, response.status);
      
      // API
      setProgress(25);
      setCurrentStep(' ...');

      if (response.ok) {
        const data = await response.json();
        console.log(' :', data);
        
        // execution_id
        if (data.execution_id) {
          executionId = data.execution_id;
          console.log(' ID:', executionId);
        }
        
        // 
        if (progressInterval) {
          clearInterval(progressInterval);
        }
        
        // 
        setProgress(100);
        setCurrentStep(' ');
        
        // 
        setScanResults(data);
        
        // 
        stopRealTimeProgress();
        
        // 
        setTimeout(() => {
          if (data.qualified_stocks && data.qualified_stocks.length > 0) {
            alert(`  ${data.qualified_stocks.length} `);
          } else {
            alert(' ');
          }
        }, 300);
        
      } else {
        const errorData = await response.json().catch(() => ({}));
        console.error(' API:', errorData);
        throw new Error(errorData.error || ` (${response.status})`);
      }

    } catch (error) {
      console.error(' :', error);
      
      // 
      if (progressInterval) {
        clearInterval(progressInterval);
      }
      
      setProgress(0);
      setCurrentStep(' ');
      setExecutingStrategy(null);
      
      // 
      stopRealTimeProgress();
      
      alert(` : ${error.message}`);
    }
  };

  // 
  const handleViewDetails = (strategy) => {
    setSelectedStrategy(strategy);
    setDetailDialogOpen(true);
  };

  // 
  const handleOpenFilter = () => {
    setFilterDialogOpen(true);
  };

  return (
    <Container maxWidth="xl" sx={{ py: 3 }}>
      {/*  */}
      <Box sx={{ textAlign: 'center', mb: 4 }}>
        <Typography variant="h3" component="h1" sx={{ fontWeight: 'bold', mb: 2 }}>
          <TrendingUp sx={{ fontSize: 'inherit', mr: 2, color: 'primary.main' }} />
          
        </Typography>
        <Typography variant="h6" color="text.secondary" sx={{ mb: 3 }}>
          
        </Typography>
        
        {/*  */}
        <Alert severity="info" sx={{ mb: 3, maxWidth: '800px', mx: 'auto' }}>
          
        </Alert>

        {/*  */}
        <Box sx={{ display: 'flex', justifyContent: 'center', gap: 2, mb: 3 }}>
          <Button
            variant="outlined"
            startIcon={<FilterList />}
            onClick={handleOpenFilter}
            sx={{ minWidth: 200 }}
          >
            
          </Button>
          <Chip 
            label={`: ${selectedMarkets.includes('all') ? '' : selectedMarkets.length + ''}`}
            color="primary"
            variant="outlined"
          />
          <Chip 
            label={`: ${selectedIndustries.includes('all') ? '' : selectedIndustries.length + ''}`}
            color="secondary"
            variant="outlined"
          />
        </Box>
      </Box>

      {/*  */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        {PRESET_STRATEGIES.map((strategy) => (
          <Grid item xs={12} sm={6} md={4} key={strategy.id}>
            <Card
              elevation={3}
              sx={{
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                transition: 'all 0.3s ease',
                '&:hover': {
                  transform: 'translateY(-4px)',
                  boxShadow: 6
                },
                border: `2px solid ${strategy.color}`,
                borderRadius: 2
              }}
            >
              {/*  */}
              <CardContent sx={{ flexGrow: 1, pb: 1 }}>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                  <Box sx={{ 
                    p: 1, 
                    borderRadius: 1, 
                    bgcolor: strategy.color, 
                    color: 'white',
                    mr: 2
                  }}>
                    {strategy.icon}
                  </Box>
                  <Box sx={{ flexGrow: 1 }}>
                    <Typography variant="h6" component="h3" sx={{ fontWeight: 'bold' }}>
                      {strategy.name}
                    </Typography>
                    <Chip 
                      label={strategy.risk_level}
                      size="small"
                      sx={{ 
                        bgcolor: getRiskColor(strategy.risk_level),
                        color: 'white',
                        fontWeight: 'bold'
                      }}
                    />
                  </Box>
                </Box>

                <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                  {strategy.description}
                </Typography>

                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
                  <Box>
                    <Typography variant="caption" color="text.secondary">
                      
                    </Typography>
                    <Typography variant="body2" sx={{ fontWeight: 'bold', color: strategy.color }}>
                      {strategy.expected_return}
                    </Typography>
                  </Box>
                  <Box sx={{ textAlign: 'right' }}>
                    <Typography variant="caption" color="text.secondary">
                      
                    </Typography>
                    <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                      {strategy.selection_criteria.length} 
                    </Typography>
                  </Box>
                </Box>

                {/*  */}
                <Box sx={{ bgcolor: 'rgba(255,255,255,0.05)', border: '1px solid rgba(255,255,255,0.1)', p: 1, borderRadius: 1 }}>
                  <Typography variant="caption" color="text.primary" sx={{ display: 'block', mb: 0.5, fontWeight: 'medium' }}>
                    
                  </Typography>
                  {strategy.selection_criteria.slice(0, 3).map((criterion, index) => (
                    <Typography key={index} variant="caption" sx={{ display: 'block', fontSize: '0.7rem', color: 'text.primary' }}>
                       {criterion}
                    </Typography>
                  ))}
                  {strategy.selection_criteria.length > 3 && (
                    <Typography variant="caption" color="primary.light" sx={{ fontStyle: 'italic', fontWeight: 'medium' }}>
                      +{strategy.selection_criteria.length - 3} ...
                    </Typography>
                  )}
                </Box>
              </CardContent>

              {/*  */}
              <CardActions sx={{ px: 2, pb: 2, pt: 0, flexDirection: 'column', gap: 1 }}>
                {/*  */}
                <Box sx={{ display: 'flex', width: '100%', gap: 1 }}>
                  <Button
                    size="small"
                    variant="outlined"
                    startIcon={<Info />}
                    onClick={() => handleViewDetails(strategy)}
                    sx={{ 
                      flex: 1,
                      borderColor: strategy.color,
                      color: strategy.color,
                      '&:hover': {
                        borderColor: strategy.color,
                        bgcolor: `${strategy.color}10`
                      }
                    }}
                  >
                    
                  </Button>
                </Box>

                {/*  */}
                <Box sx={{ display: 'flex', width: '100%', gap: 1 }}>
                  <Button
                    size="small"
                    variant="contained"
                    startIcon={<PlayArrow />}
                    onClick={() => executeStrategy(strategy, 'quick')}
                    disabled={!!executingStrategy}
                    sx={{ 
                      flex: 1,
                      bgcolor: strategy.color,
                      '&:hover': {
                        bgcolor: strategy.color,
                        filter: 'brightness(0.9)'
                      }
                    }}
                  >
                    
                  </Button>
                  <Button
                    size="small"
                    variant="contained"
                    startIcon={<Speed />}
                    onClick={() => executeStrategy(strategy, 'full')}
                    disabled={!!executingStrategy}
                    sx={{ 
                      flex: 1,
                      bgcolor: strategy.color,
                      '&:hover': {
                        bgcolor: strategy.color,
                        filter: 'brightness(0.9)'
                      }
                    }}
                  >
                    
                  </Button>
                </Box>

                {/*  */}
                <Typography variant="caption" color="text.secondary" sx={{ textAlign: 'center', fontSize: '0.65rem' }}>
                  : 50 | : 4000+A
                </Typography>
              </CardActions>
            </Card>
          </Grid>
        ))}
      </Grid>

      {/*  */}
      {executingStrategy && (
        <RealTimeProgress
          open={!!executingStrategy}
          strategy={executingStrategy}
          progress={progress}
          currentStep={currentStep}
          isFullMarket={isFullMarket}
          realTimeData={realTimeData}
          onClose={() => {
            setExecutingStrategy(null);
            stopRealTimeProgress();
          }}
        />
      )}

      {/*  */}
      <Dialog 
        open={filterDialogOpen} 
        onClose={() => setFilterDialogOpen(false)}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <FilterList sx={{ mr: 1 }} />
            
          </Box>
        </DialogTitle>
        <DialogContent dividers>
          {/*  */}
          <Accordion defaultExpanded>
            <AccordionSummary expandIcon={<ExpandMore />}>
              <Typography variant="h6"> </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Grid container spacing={2}>
                {MARKETS.map((market) => (
                  <Grid item xs={6} sm={4} key={market.id}>
                    <FormControlLabel
                      control={
                        <Checkbox
                          checked={selectedMarkets.includes(market.id)}
                          onChange={() => handleMarketChange(market.id)}
                          sx={{ color: market.color }}
                        />
                      }
                      label={
                        <Box>
                          <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                            {market.name}
                          </Typography>
                          <Typography variant="caption" color="text.secondary">
                            {market.description}
                          </Typography>
                        </Box>
                      }
                    />
                  </Grid>
                ))}
              </Grid>
            </AccordionDetails>
          </Accordion>

          {/*  */}
          <Accordion defaultExpanded>
            <AccordionSummary expandIcon={<ExpandMore />}>
              <Typography variant="h6"> </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Grid container spacing={2}>
                {INDUSTRIES.map((industry) => (
                  <Grid item xs={6} sm={4} key={industry.id}>
                    <FormControlLabel
                      control={
                        <Checkbox
                          checked={selectedIndustries.includes(industry.id)}
                          onChange={() => handleIndustryChange(industry.id)}
                          sx={{ color: industry.color }}
                        />
                      }
                      label={
                        <Box sx={{ display: 'flex', alignItems: 'center' }}>
                          <Box sx={{ color: industry.color, mr: 1 }}>
                            {industry.icon}
                          </Box>
                          <Typography variant="body2">
                            {industry.name}
                          </Typography>
                        </Box>
                      }
                    />
                  </Grid>
                ))}
              </Grid>
            </AccordionDetails>
          </Accordion>

          {/*  */}
          <Paper sx={{ p: 2, mt: 2, bgcolor: 'grey.50' }}>
            <Typography variant="subtitle2" sx={{ mb: 1 }}></Typography>
            <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap' }}>
              {selectedMarkets.map(marketId => {
                const market = MARKETS.find(m => m.id === marketId);
                return market ? (
                  <Chip 
                    key={marketId}
                    label={market.name}
                    size="small"
                    sx={{ bgcolor: market.color, color: 'white' }}
                  />
                ) : null;
              })}
              {selectedIndustries.map(industryId => {
                const industry = INDUSTRIES.find(i => i.id === industryId);
                return industry ? (
                  <Chip 
                    key={industryId}
                    label={industry.name}
                    size="small"
                    variant="outlined"
                    sx={{ borderColor: industry.color, color: industry.color }}
                  />
                ) : null;
              })}
            </Box>
          </Paper>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setFilterDialogOpen(false)}>
            
          </Button>
          <Button 
            variant="contained" 
            onClick={() => setFilterDialogOpen(false)}
          >
            
          </Button>
        </DialogActions>
      </Dialog>

      {/*  */}
      <Dialog 
        open={detailDialogOpen} 
        onClose={() => setDetailDialogOpen(false)}
        maxWidth="md"
        fullWidth
      >
        {selectedStrategy && (
          <>
            <DialogTitle>
              <Box sx={{ display: 'flex', alignItems: 'center' }}>
                <Box sx={{ 
                  p: 1, 
                  borderRadius: 1, 
                  bgcolor: selectedStrategy.color, 
                  color: 'white',
                  mr: 2
                }}>
                  {selectedStrategy.icon}
                </Box>
                <Box>
                  <Typography variant="h6">{selectedStrategy.name}</Typography>
                  <Chip 
                    label={selectedStrategy.risk_level}
                    size="small"
                    sx={{ 
                      bgcolor: getRiskColor(selectedStrategy.risk_level),
                      color: 'white'
                    }}
                  />
                </Box>
              </Box>
            </DialogTitle>
            <DialogContent dividers>
              <Typography variant="body1" sx={{ mb: 3 }}>
                {selectedStrategy.description}
              </Typography>
              
              <Typography variant="h6" sx={{ mb: 2 }}></Typography>
              <List>
                {selectedStrategy.selection_criteria.map((criterion, index) => (
                  <ListItem key={index}>
                    <ListItemText primary={criterion} />
                  </ListItem>
                ))}
              </List>
              
              <Divider sx={{ my: 2 }} />
              
              <Typography variant="h6" sx={{ mb: 2 }}></Typography>
              <Grid container spacing={2}>
                {Object.entries(selectedStrategy.parameters).map(([key, value]) => (
                  <Grid item xs={6} key={key}>
                    <Typography variant="body2">
                      <strong>{key}:</strong> {value}
                    </Typography>
                  </Grid>
                ))}
              </Grid>
            </DialogContent>
            <DialogActions>
              <Button onClick={() => setDetailDialogOpen(false)}>
                
              </Button>
              <Button 
                variant="contained" 
                startIcon={<PlayArrow />}
                onClick={() => {
                  setDetailDialogOpen(false);
                  executeStrategy(selectedStrategy, 'quick');
                }}
                sx={{ bgcolor: selectedStrategy.color }}
              >
                
              </Button>
            </DialogActions>
          </>
        )}
      </Dialog>

      {/*  */}
      {scanResults && (
        <Paper sx={{ p: 3, mt: 4 }}>
          <Typography variant="h5" sx={{ mb: 2 }}>
             
          </Typography>
          
          {scanResults.qualified_stocks && scanResults.qualified_stocks.length > 0 ? (
            <>
              <Alert severity="success" sx={{ mb: 2 }}>
                  {scanResults.qualified_stocks.length} 
              </Alert>
              
              {/*  */}
              <Typography variant="body2" color="text.secondary">
                ...
              </Typography>
            </>
          ) : (
            <Alert severity="warning">
               
            </Alert>
          )}
        </Paper>
      )}
    </Container>
  );
};

export default PresetStrategies; 