import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  LinearProgress,
  Card,
  CardContent,
  Grid,
  Chip,
  Alert,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  CircularProgress,
  Paper,
  IconButton
} from '@mui/material';
import {
  TrendingUp,
  CheckCircle,
  Schedule,
  Speed,
  Assessment,
  DataUsage,
  Timeline,
  Close
} from '@mui/icons-material';

const RealTimeProgress = ({ 
  open = false,
  onClose = () => {},
  strategy,
  progress, 
  currentStep, 
  isFullMarket = false,
  realTimeData = {}
}) => {
  const [animatedProgress, setAnimatedProgress] = useState(0);
  const [stepHistory, setStepHistory] = useState([]);

  // 
  useEffect(() => {
    const timer = setTimeout(() => {
      setAnimatedProgress(progress);
    }, 100);
    return () => clearTimeout(timer);
  }, [progress]);

  // 
  useEffect(() => {
    if (currentStep && !stepHistory.includes(currentStep)) {
      setStepHistory(prev => [...prev, currentStep].slice(-6)); // 6
    }
  }, [currentStep, stepHistory]);

  // 
  const getPhaseColor = (progress) => {
    if (progress < 20) return '#2196f3'; //  - 
    if (progress < 40) return '#ff9800'; //  - 
    if (progress < 60) return '#9c27b0'; //  - 
    if (progress < 80) return '#f44336'; //  - 
    return '#4caf50'; //  - 
  };

  // 
  const getCurrentPhase = (progress) => {
    if (progress < 20) return '';
    if (progress < 40) return '';
    if (progress < 60) return '';
    if (progress < 80) return '';
    return '';
  };

  // 
  const getEstimatedTime = (progress, isFullMarket) => {
    if (progress === 0) return isFullMarket ? '90-120' : '2-3';
    
    const baseTime = isFullMarket ? 90 : 3; // 
    const remaining = ((100 - progress) / 100) * baseTime;
    
    if (remaining < 1) return '1';
    if (remaining < 60) return `${Math.ceil(remaining)}`;
    return `${Math.ceil(remaining / 60)}${Math.ceil(remaining % 60)}`;
  };

  const currentPhase = getCurrentPhase(animatedProgress);
  const phaseColor = getPhaseColor(animatedProgress);
  const estimatedTime = getEstimatedTime(animatedProgress, isFullMarket);

  if (!open || !strategy) return null;

  return (
    <Box sx={{ 
      position: 'fixed', 
      top: 0, 
      left: 0, 
      width: '100%', 
      height: '100%', 
      bgcolor: 'rgba(0,0,0,0.8)', 
      zIndex: 9999,
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center'
    }}>
      {/*  */}
      <Card sx={{ 
        width: '90%', 
        maxWidth: 800, 
        maxHeight: '90vh',
        overflow: 'auto',
        background: 'linear-gradient(135deg, rgba(33, 150, 243, 0.1) 0%, rgba(33, 150, 243, 0.05) 100%)',
        border: '1px solid rgba(33, 150, 243, 0.2)',
        borderRadius: 3
      }}>
        <CardContent sx={{ p: 4 }}>
          {/*  */}
          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', mb: 3 }}>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Box sx={{ 
                p: 1.5, 
                borderRadius: 2, 
                bgcolor: strategy.color,
                color: 'white',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center'
              }}>
                {strategy.icon}
              </Box>
              <Box>
                <Typography variant="h5" sx={{ fontWeight: 'bold', color: 'primary.main' }}>
                  {strategy.name} - 
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  {strategy.description}
                </Typography>
              </Box>
            </Box>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Box sx={{ textAlign: 'right' }}>
                <Typography variant="h4" sx={{ fontWeight: 'bold', color: phaseColor }}>
                  {animatedProgress.toFixed(0)}%
                </Typography>
                <Typography variant="caption" color="text.secondary">
                  {isFullMarket ? '' : ''}
                </Typography>
              </Box>
              <IconButton onClick={onClose} sx={{ color: 'text.secondary' }}>
                <Close />
              </IconButton>
            </Box>
          </Box>

          {/*  */}
          <Box sx={{ mb: 3 }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
              <Typography variant="body2" sx={{ fontWeight: 'medium' }}>
                {currentPhase}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                : {estimatedTime}
              </Typography>
            </Box>
            <LinearProgress
              variant="determinate"
              value={animatedProgress}
              sx={{
                height: 8,
                borderRadius: 4,
                backgroundColor: 'rgba(0,0,0,0.1)',
                '& .MuiLinearProgress-bar': {
                  backgroundColor: phaseColor,
                  borderRadius: 4,
                }
              }}
            />
          </Box>

          {/*  */}
          <Alert 
            severity="info" 
            icon={<CircularProgress size={20} />}
            sx={{ mb: 3, bgcolor: 'rgba(33, 150, 243, 0.1)' }}
          >
            <Typography variant="body2">
              {currentStep || '...'}
            </Typography>
          </Alert>

          {/*  */}
          <Grid container spacing={2} sx={{ mb: 3 }}>
            <Grid item xs={6} sm={3}>
              <Paper sx={{ p: 2, textAlign: 'center', bgcolor: 'rgba(76, 175, 80, 0.1)' }}>
                <Assessment sx={{ color: '#4caf50', mb: 1 }} />
                <Typography variant="h6" sx={{ fontWeight: 'bold', color: '#4caf50' }}>
                  {realTimeData.qualifiedCount || 0}
                </Typography>
                <Typography variant="caption" color="text.secondary">
                  
                </Typography>
              </Paper>
            </Grid>
            <Grid item xs={6} sm={3}>
              <Paper sx={{ p: 2, textAlign: 'center', bgcolor: 'rgba(33, 150, 243, 0.1)' }}>
                <DataUsage sx={{ color: '#2196f3', mb: 1 }} />
                <Typography variant="h6" sx={{ fontWeight: 'bold', color: '#2196f3' }}>
                  100%
                </Typography>
                <Typography variant="caption" color="text.secondary">
                  
                </Typography>
              </Paper>
            </Grid>
            <Grid item xs={6} sm={3}>
              <Paper sx={{ p: 2, textAlign: 'center', bgcolor: 'rgba(255, 152, 0, 0.1)' }}>
                <Speed sx={{ color: '#ff9800', mb: 1 }} />
                <Typography variant="h6" sx={{ fontWeight: 'bold', color: '#ff9800' }}>
                  {realTimeData.analysisSpeed || '5.0'}
                </Typography>
                <Typography variant="caption" color="text.secondary">
                  /
                </Typography>
              </Paper>
            </Grid>
            <Grid item xs={6} sm={3}>
              <Paper sx={{ p: 2, textAlign: 'center', bgcolor: 'rgba(156, 39, 176, 0.1)' }}>
                <Timeline sx={{ color: '#9c27b0', mb: 1 }} />
                <Typography variant="h6" sx={{ fontWeight: 'bold', color: '#9c27b0' }}>
                  {realTimeData.dataSource || 'TuShare'}
                </Typography>
                <Typography variant="caption" color="text.secondary">
                  
                </Typography>
              </Paper>
            </Grid>
          </Grid>

          {/*  */}
          {realTimeData.currentStock && (
            <Box sx={{ mb: 3 }}>
              <Typography variant="subtitle2" sx={{ mb: 1, display: 'flex', alignItems: 'center' }}>
                <TrendingUp sx={{ mr: 1, color: 'primary.main' }} />
                
              </Typography>
              <Chip 
                label={realTimeData.currentStock}
                color="primary"
                variant="outlined"
                sx={{ fontFamily: 'monospace' }}
              />
            </Box>
          )}

          {/*  */}
          {stepHistory.length > 0 && (
            <Box>
              <Typography variant="subtitle2" sx={{ mb: 1, display: 'flex', alignItems: 'center' }}>
                <Schedule sx={{ mr: 1, color: 'text.secondary' }} />
                
              </Typography>
              <List dense sx={{ bgcolor: 'rgba(0,0,0,0.02)', borderRadius: 1, maxHeight: 200, overflow: 'auto' }}>
                {stepHistory.map((step, index) => (
                  <ListItem key={index}>
                    <ListItemIcon sx={{ minWidth: 32 }}>
                      <CheckCircle sx={{ fontSize: 16, color: '#4caf50' }} />
                    </ListItemIcon>
                    <ListItemText 
                      primary={step}
                      primaryTypographyProps={{ variant: 'body2' }}
                    />
                  </ListItem>
                ))}
              </List>
            </Box>
          )}
        </CardContent>
      </Card>
    </Box>
  );
};

export default RealTimeProgress; 