import axios from 'axios';

const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';

const api = axios.create({
  baseURL: API_BASE_URL,
  timeout: 120000, // 2
  headers: {
    'Content-Type': 'application/json',
  },
});

// 
api.interceptors.request.use(
  (config) => {
    console.log('API:', config.method?.toUpperCase(), config.url);
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// 
api.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    console.error('API:', error);
    return Promise.reject(error);
  }
);

// API
export const fetchTradingSignals = async (stockCode) => {
  try {
    const response = await api.post('/trading_signals', {
      stock_code: stockCode
    }, {
      timeout: 180000, // 3
    });
    return response.data;
  } catch (error) {
    console.error(':', error);
    throw error;
  }
};

export const stockAPI = {
  // 
  searchStocks: async (query, limit = 10) => {
    try {
      const response = await api.get('/search_stocks', {
        params: { q: query, limit }
      });
      return response.data;
    } catch (error) {
      throw new Error('');
    }
  },

  // 
  getStockInfo: async (codeOrName) => {
    try {
      const response = await api.get('/get_stock_info', {
        params: { code_or_name: codeOrName }
      });
      return response.data;
    } catch (error) {
      throw new Error('');
    }
  },

  // 
  generateTradingSignals: async (formData) => {
    try {
      const response = await api.post('/trading_signals', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        timeout: 180000, // 3
      });
      return response.data;
    } catch (error) {
      throw new Error('');
    }
  },
};

// API
export const strategyAPI = {
  // 
  getStrategyConfig: async (strategyId) => {
    try {
      const response = await api.get(`/api/strategies/config/${strategyId}`);
      return response.data;
    } catch (error) {
      throw new Error('');
    }
  },

  // 
  updateStrategyConfig: async (strategyId, params) => {
    try {
      const response = await api.post(`/api/strategies/config/${strategyId}`, {
        params
      });
      return response.data;
    } catch (error) {
      throw new Error('');
    }
  },

  // 
  executeStrategy: async (strategyId, stockCode, startDate, endDate) => {
    try {
      const response = await api.post('/api/strategies/execute', {
        strategy_id: strategyId,
        stock_code: stockCode,
        start_date: startDate,
        end_date: endDate
      }, {
        timeout: 120000, // 2
      });
      return response.data;
    } catch (error) {
      throw new Error('');
    }
  },

  // 
  runBacktest: async (strategyId, stockCode, startDate, endDate, initialCapital = 100000) => {
    try {
      const response = await api.post('/api/strategies/backtest', {
        strategy_id: strategyId,
        stock_code: stockCode,
        start_date: startDate,
        end_date: endDate,
        initial_capital: initialCapital
      }, {
        timeout: 180000, // 3
      });
      return response.data;
    } catch (error) {
      throw new Error('');
    }
  },

  // 
  getStrategiesList: async () => {
    try {
      const response = await api.get('/api/strategies/list');
      return response.data;
    } catch (error) {
      throw new Error('');
    }
  },

  // 50
  executeMarketScan: async (strategyId, startDate, endDate, maxStocks = 50, minScore = 60.0) => {
    try {
      const response = await api.post('/api/strategies/market-scan', {
        strategy_id: strategyId,
        start_date: startDate,
        end_date: endDate,
        max_stocks: maxStocks,
        min_score: minScore
      }, {
        timeout: 300000, // 5
      });
      return response.data;
    } catch (error) {
      throw new Error('');
    }
  },

  // A
  executeFullMarketScan: async (strategyId, startDate, endDate, minScore = 60.0, batchSize = 100) => {
    try {
      const response = await api.post('/api/strategies/full-market-scan', {
        strategy_id: strategyId,
        start_date: startDate,
        end_date: endDate,
        min_score: minScore,
        batch_size: batchSize
      }, {
        timeout: 7200000, // 2A
      });
      return response.data;
    } catch (error) {
      throw new Error('A');
    }
  },

  // Excel
  exportExcel: async (scanResults) => {
    try {
      const response = await api.post('/api/strategies/export-excel', {
        scan_results: scanResults
      }, {
        responseType: 'blob',
        timeout: 60000
      });
      
      // 
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      
      // 
      const contentDisposition = response.headers['content-disposition'];
      let filename = 'strategy_analysis.xlsx';
      if (contentDisposition) {
        const matches = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/.exec(contentDisposition);
        if (matches != null && matches[1]) {
          filename = matches[1].replace(/['"]/g, '');
        }
      }
      
      link.setAttribute('download', filename);
      document.body.appendChild(link);
      link.click();
      link.remove();
      window.URL.revokeObjectURL(url);
      
      return { success: true, filename };
    } catch (error) {
      throw new Error('Excel');
    }
  },

  // CSV
  exportCSV: async (scanResults) => {
    try {
      const response = await api.post('/api/strategies/export-csv', {
        scan_results: scanResults
      }, {
        responseType: 'blob',
        timeout: 60000
      });
      
      // 
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      
      // 
      const contentDisposition = response.headers['content-disposition'];
      let filename = 'strategy_analysis.csv';
      if (contentDisposition) {
        const matches = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/.exec(contentDisposition);
        if (matches != null && matches[1]) {
          filename = matches[1].replace(/['"]/g, '');
        }
      }
      
      link.setAttribute('download', filename);
      document.body.appendChild(link);
      link.click();
      link.remove();
      window.URL.revokeObjectURL(url);
      
      return { success: true, filename };
    } catch (error) {
      throw new Error('CSV');
    }
  },
};

export default api; 