const STRATEGY_CATEGORIES = [
  { label: '', value: 'all', icon: <Star /> },
  { label: '', value: 'value', icon: <Assessment /> },
  { label: '', value: 'growth', icon: <TrendingUp /> },
  { label: '', value: 'dividend', icon: <MonetizationOn /> },
  { label: '', value: 'momentum', icon: <Speed /> },
  { label: '', value: 'rotation', icon: <CompareArrows /> },
  { label: '', value: 'technology', icon: <Psychology /> },
  { label: '', value: 'consumer', icon: <AccountBalance /> },
  { label: '', value: 'balanced', icon: <Timeline /> },
  { label: '', value: 'quantitative', icon: <Equalizer /> },
  { label: 'ESG', value: 'esg', icon: <PieChart /> }
];

const AdvancedStrategyConfig = () => {
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [selectedStrategy, setSelectedStrategy] = useState(null);
  const [detailDialogOpen, setDetailDialogOpen] = useState(false);
  const [configDialogOpen, setConfigDialogOpen] = useState(false);
  const [scanning, setScanning] = useState(false);
  const [scanResults, setScanResults] = useState(null);
  const [progress, setProgress] = useState(0);
  const [strategyParams, setStrategyParams] = useState({});
  const [activeTab, setActiveTab] = useState(0);

  // 
  const filteredStrategies = selectedCategory === 'all' 
    ? ADVANCED_STRATEGIES 
    : ADVANCED_STRATEGIES.filter(strategy => strategy.category === selectedCategory);

  // 
  const handleViewDetails = (strategy) => {
    setSelectedStrategy(strategy);
    setDetailDialogOpen(true);
  };

  // 
  const handleConfigStrategy = (strategy) => {
    setSelectedStrategy(strategy);
    setStrategyParams(strategy.parameters);
    setConfigDialogOpen(true);
  };

  // 
  const handleParamChange = (paramName, newValue) => {
    setStrategyParams(prev => ({
      ...prev,
      [paramName]: { ...prev[paramName], value: newValue }
    }));
  };

  // 
  const handleExecuteStrategy = async (strategy) => {
    setScanning(true);
    setProgress(0);
    setScanResults(null);

    try {
      // 
      const progressInterval = setInterval(() => {
        setProgress(prev => {
          if (prev >= 95) {
            clearInterval(progressInterval);
            return 95;
          }
          return prev + Math.random() * 8;
        });
      }, 400);

      // API
      const response = await fetch('/api/advanced-strategies/execute', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          strategy_id: strategy.id,
          strategy_name: strategy.name,
          parameters: strategyParams || strategy.parameters,
          max_stocks: 100
        })
      });

      if (response.ok) {
        const data = await response.json();
        setScanResults(data);
        setProgress(100);
        setConfigDialogOpen(false);
      } else {
        throw new Error('');
      }

      clearInterval(progressInterval);
    } catch (error) {
      console.error(':', error);
      alert('');
    } finally {
      setScanning(false);
    }
  };

  // 
  const getRiskColor = (riskLevel) => {
    switch (riskLevel) {
      case '': return '#4caf50';
      case '': return '#ff9800';
      case '': return '#f57c00';
      case '': return '#f44336';
      default: return '#9e9e9e';
    }
  };

  // 
  const getComplexityColor = (complexity) => {
    switch (complexity) {
      case '': return '#4caf50';
      case '': return '#ff9800';
      case '': return '#f44336';
      default: return '#9e9e9e';
    }
  };

  return (
    <Container maxWidth="xl" sx={{ py: 3 }}>
      {/*  */}
      <Box sx={{ mb: 4 }}>
        <Typography 
          variant="h4" 
          component="h1" 
          gutterBottom
          sx={{ 
            fontWeight: 'bold',
            background: 'linear-gradient(45deg, #1976d2, #42a5f5)',
            backgroundClip: 'text',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent',
            mb: 1
          }}
        >
           
        </Typography>
        <Typography variant="h6" color="text.secondary" gutterBottom>
          
        </Typography>
      </Box>

      {/*  */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        <Grid item xs={12} sm={3}>
          <Card elevation={3} sx={{ textAlign: 'center', bgcolor: 'rgba(33, 150, 243, 0.1)', border: '1px solid rgba(33, 150, 243, 0.3)' }}>
            <CardContent>
              <Typography variant="h4" color="primary.main" sx={{ fontWeight: 'bold' }}>
                {ADVANCED_STRATEGIES.length}
              </Typography>
              <Typography variant="body2" color="primary.light" sx={{ fontWeight: 'medium' }}>
                
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} sm={3}>
          <Card elevation={3} sx={{ textAlign: 'center', bgcolor: 'rgba(156, 39, 176, 0.1)', border: '1px solid rgba(156, 39, 176, 0.3)' }}>
            <CardContent>
              <Typography variant="h4" color="secondary.main" sx={{ fontWeight: 'bold' }}>
                {STRATEGY_CATEGORIES.length - 1}
              </Typography>
              <Typography variant="body2" color="secondary.light" sx={{ fontWeight: 'medium' }}>
                
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} sm={3}>
          <Card elevation={3} sx={{ textAlign: 'center', bgcolor: 'rgba(76, 175, 80, 0.1)', border: '1px solid rgba(76, 175, 80, 0.3)' }}>
            <CardContent>
              <Typography variant="h4" color="success.main" sx={{ fontWeight: 'bold' }}>
                4000+
              </Typography>
              <Typography variant="body2" color="success.light" sx={{ fontWeight: 'medium' }}>
                
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} sm={3}>
          <Card elevation={3} sx={{ textAlign: 'center', bgcolor: 'rgba(255, 152, 0, 0.1)', border: '1px solid rgba(255, 152, 0, 0.3)' }}>
            <CardContent>
              <Typography variant="h4" color="warning.main" sx={{ fontWeight: 'bold' }}>
                100%
              </Typography>
              <Typography variant="body2" color="warning.light" sx={{ fontWeight: 'medium' }}>
                
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/*  */}
      <Paper elevation={2} sx={{ mb: 3 }}>
        <Tabs
          value={selectedCategory}
          onChange={(e, newValue) => setSelectedCategory(newValue)}
          variant="scrollable"
          scrollButtons="auto"
          sx={{ px: 2 }}
        >
          {STRATEGY_CATEGORIES.map((category) => (
            <Tab
              key={category.value}
              value={category.value}
              label={category.label}
              icon={category.icon}
              iconPosition="start"
            />
          ))}
        </Tabs>
      </Paper>

      {/*  */}
      <Grid container spacing={3}>
        {filteredStrategies.map((strategy) => (
          <Grid item xs={12} md={6} lg={4} key={strategy.id}>
            <Card 
              elevation={3} 
              sx={{ 
                height: '100%', 
                display: 'flex', 
                flexDirection: 'column',
                transition: 'all 0.3s ease-in-out',
                '&:hover': {
                  transform: 'translateY(-4px)',
                  boxShadow: 6
                }
              }}
            >
              <CardContent sx={{ flex: 1 }}>
                {/*  */}
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                  <Box sx={{ color: strategy.color, mr: 1 }}>
                    {strategy.icon}
                  </Box>
                  <Typography variant="h6" component="h3" sx={{ fontWeight: 'bold' }}>
                    {strategy.name}
                  </Typography>
                </Box>

                {/*  */}
                <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                  {strategy.description}
                </Typography>

                {/*  */}
                <Box sx={{ display: 'flex', gap: 1, mb: 2, flexWrap: 'wrap' }}>
                  <Chip 
                    label={strategy.risk_level} 
                    size="small" 
                    sx={{ 
                      bgcolor: getRiskColor(strategy.risk_level),
                      color: 'white',
                      fontWeight: 'bold'
                    }}
                  />
                  <Chip 
                    label={strategy.complexity} 
                    size="small" 
                    variant="outlined"
                    sx={{ 
                      borderColor: getComplexityColor(strategy.complexity),
                      color: getComplexityColor(strategy.complexity)
                    }}
                  />
                  <Chip 
                    label={strategy.expected_return} 
                    size="small" 
                    color="primary"
                    variant="outlined"
                  />
                </Box>

                {/*  */}
                {strategy.backtest_performance && (
                  <Box sx={{ bgcolor: '#f5f5f5', p: 1.5, borderRadius: 1, mb: 2 }}>
                    <Typography variant="body2" color="text.secondary" gutterBottom>
                       
                    </Typography>
                    <Grid container spacing={1}>
                      <Grid item xs={6}>
                        <Typography variant="caption" color="text.secondary">
                          
                        </Typography>
                        <Typography variant="body2" sx={{ fontWeight: 'bold', color: 'success.main' }}>
                          {strategy.backtest_performance.annual_return}
                        </Typography>
                      </Grid>
                      <Grid item xs={6}>
                        <Typography variant="caption" color="text.secondary">
                          
                        </Typography>
                        <Typography variant="body2" sx={{ fontWeight: 'bold', color: 'error.main' }}>
                          {strategy.backtest_performance.max_drawdown}
                        </Typography>
                      </Grid>
                      <Grid item xs={6}>
                        <Typography variant="caption" color="text.secondary">
                          
                        </Typography>
                        <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                          {strategy.backtest_performance.sharpe_ratio}
                        </Typography>
                      </Grid>
                      <Grid item xs={6}>
                        <Typography variant="caption" color="text.secondary">
                          
                        </Typography>
                        <Typography variant="body2" sx={{ fontWeight: 'bold', color: 'primary.main' }}>
                          {strategy.backtest_performance.win_rate}
                        </Typography>
                      </Grid>
                    </Grid>
                  </Box>
                )}
              </CardContent>

              <CardActions sx={{ p: 2, pt: 0 }}>
                <Button
                  size="small"
                  startIcon={<Visibility />}
                  onClick={() => handleViewDetails(strategy)}
                >
                  
                </Button>
                <Button
                  size="small"
                  startIcon={<Settings />}
                  onClick={() => handleConfigStrategy(strategy)}
                >
                  
                </Button>
                <Button
                  size="small"
                  variant="contained"
                  startIcon={<PlayArrow />}
                  onClick={() => handleExecuteStrategy(strategy)}
                  sx={{ ml: 'auto' }}
                >
                  
                </Button>
              </CardActions>
            </Card>
          </Grid>
        ))}
      </Grid>

      {/*  */}
      <Dialog 
        open={detailDialogOpen} 
        onClose={() => setDetailDialogOpen(false)}
        maxWidth="md"
        fullWidth
      >
        {selectedStrategy && (
          <>
            <DialogTitle sx={{ 
              bgcolor: 'primary.main', 
              color: 'white',
              display: 'flex',
              alignItems: 'center',
              gap: 1
            }}>
              {selectedStrategy.icon}
              {selectedStrategy.name}
            </DialogTitle>
            <DialogContent>
              <Box sx={{ mt: 2 }}>
                <Typography variant="body1" paragraph>
                  {selectedStrategy.description}
                </Typography>

                <Divider sx={{ my: 2 }} />

                <Typography variant="h6" gutterBottom>
                   
                </Typography>
                <Grid container spacing={1}>
                  {selectedStrategy.selection_criteria.map((criteria, index) => (
                    <Grid item xs={12} sm={6} key={index}>
                      <Typography variant="body2" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                        <Chip label="" size="small" color="success" />
                        {criteria}
                      </Typography>
                    </Grid>
                  ))}
                </Grid>

                {selectedStrategy.backtest_performance && (
                  <>
                    <Divider sx={{ my: 2 }} />
                    <Typography variant="h6" gutterBottom>
                       
                    </Typography>
                    <TableContainer component={Paper} variant="outlined">
                      <Table size="small">
                        <TableBody>
                          <TableRow>
                            <TableCell><strong></strong></TableCell>
                            <TableCell sx={{ color: 'success.main', fontWeight: 'bold' }}>
                              {selectedStrategy.backtest_performance.annual_return}
                            </TableCell>
                          </TableRow>
                          <TableRow>
                            <TableCell><strong></strong></TableCell>
                            <TableCell sx={{ color: 'error.main', fontWeight: 'bold' }}>
                              {selectedStrategy.backtest_performance.max_drawdown}
                            </TableCell>
                          </TableRow>
                          <TableRow>
                            <TableCell><strong></strong></TableCell>
                            <TableCell sx={{ fontWeight: 'bold' }}>
                              {selectedStrategy.backtest_performance.sharpe_ratio}
                            </TableCell>
                          </TableRow>
                          <TableRow>
                            <TableCell><strong></strong></TableCell>
                            <TableCell sx={{ color: 'primary.main', fontWeight: 'bold' }}>
                              {selectedStrategy.backtest_performance.win_rate}
                            </TableCell>
                          </TableRow>
                        </TableBody>
                      </Table>
                    </TableContainer>
                  </>
                )}
              </Box>
            </DialogContent>
            <DialogActions>
              <Button onClick={() => setDetailDialogOpen(false)}>
                
              </Button>
              <Button 
                variant="contained" 
                onClick={() => {
                  setDetailDialogOpen(false);
                  handleConfigStrategy(selectedStrategy);
                }}
                startIcon={<Settings />}
              >
                
              </Button>
            </DialogActions>
          </>
        )}
      </Dialog>

      {/*  */}
      <Dialog 
        open={configDialogOpen} 
        onClose={() => setConfigDialogOpen(false)}
        maxWidth="md"
        fullWidth
      >
        {selectedStrategy && (
          <>
            <DialogTitle sx={{ 
              bgcolor: selectedStrategy.color, 
              color: 'white',
              display: 'flex',
              alignItems: 'center',
              gap: 1
            }}>
              <Settings />
              : {selectedStrategy.name}
            </DialogTitle>
            <DialogContent>
              <Box sx={{ mt: 2 }}>
                <Typography variant="body2" color="text.secondary" gutterBottom>
                  
                </Typography>

                <Grid container spacing={3}>
                  {Object.entries(strategyParams).map(([paramName, paramConfig]) => (
                    <Grid item xs={12} sm={6} key={paramName}>
                      <Box sx={{ mb: 2 }}>
                        <Typography variant="body2" gutterBottom>
                          {paramConfig.desc}
                        </Typography>
                        <Slider
                          value={paramConfig.value}
                          min={paramConfig.min}
                          max={paramConfig.max}
                          step={(paramConfig.max - paramConfig.min) / 100}
                          onChange={(e, newValue) => handleParamChange(paramName, newValue)}
                          valueLabelDisplay="auto"
                          marks={[
                            { value: paramConfig.min, label: paramConfig.min },
                            { value: paramConfig.max, label: paramConfig.max }
                          ]}
                        />
                        <TextField
                          size="small"
                          type="number"
                          value={paramConfig.value}
                          onChange={(e) => handleParamChange(paramName, parseFloat(e.target.value))}
                          inputProps={{
                            min: paramConfig.min,
                            max: paramConfig.max,
                            step: 0.1
                          }}
                          sx={{ mt: 1, width: '100px' }}
                        />
                      </Box>
                    </Grid>
                  ))}
                </Grid>
              </Box>
            </DialogContent>
            <DialogActions>
              <Button onClick={() => setConfigDialogOpen(false)}>
                
              </Button>
              <Button 
                variant="contained" 
                onClick={() => handleExecuteStrategy(selectedStrategy)}
                startIcon={<PlayArrow />}
                disabled={scanning}
              >
                {scanning ? '...' : ''}
              </Button>
            </DialogActions>
          </>
        )}
      </Dialog>

      {/*  */}
      <Dialog open={scanning} maxWidth="sm" fullWidth>
        <DialogTitle>...</DialogTitle>
        <DialogContent>
          <Box sx={{ mt: 2 }}>
            <Typography variant="body2" gutterBottom>
              ...
            </Typography>
            <LinearProgress 
              variant="determinate" 
              value={progress} 
              sx={{ mt: 2, mb: 1 }}
            />
            <Typography variant="body2" color="text.secondary">
              : {Math.round(progress)}%
            </Typography>
          </Box>
        </DialogContent>
      </Dialog>

      {/*  */}
      <StrategyResultDisplay
        open={!!scanResults}
        onClose={() => setScanResults(null)}
        scanResults={scanResults}
      />
    </Container>
  );
};

export default AdvancedStrategyConfig; 